*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
  max-width: 120rem;
  margin: 0 auto;
  padding: 0 2rem;
}

body {
  min-height: 100%;
  background-color: #1e1e1e;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 330px) {
  body {
    max-width: 80rem;
    margin: 0 auto;
    padding: 0 2rem;
  }
}
@media (min-width: 768px) {
  body {
    max-width: 120rem;
    margin: 0 auto;
    padding: 0 2rem;
  }
}

h1,
h2,
h3 {
  margin: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

li {
  list-style: none;
}

img {
  max-width: 100%;
}

main > section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
}

h1,
h2 {
  font-family: "Lato", sans-serif;
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .navegacion--flotante {
    position: fixed;
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    background-color: rgba(90, 88, 88, 0.5137254902);
    padding: 0;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(1rem);
    transition: all 0.3s ease-in-out;
    border-radius: 20rem;
  }
  .navegacion--flotante .navegacion--menu--lista {
    padding: 0.7rem;
    padding-right: 1.2rem;
  }
  .navegacion--flotante .navegacion--menu--enlace:not(:hover),
  .navegacion--flotante .navegacion--logo--texto:not(:hover) {
    font-size: 1.2rem;
    margin-left: 1.5rem;
  }
}
.navegacion--logo {
  display: none;
}
@media (min-width: 768px) {
  .navegacion--logo {
    display: inline-block;
  }
}
.navegacion--logo--texto {
  color: #ffffff;
  margin-left: 2rem;
  font-weight: 700;
  font-size: 3rem;
  font-family: "Lato", sans-serif;
}
.navegacion--logo--texto:hover {
  color: #be6bff;
  font-size: 3.5rem;
  transition: all 0.3s ease-in-out;
}
.navegacion--logo--texto:not(:hover) {
  color: #ffffff;
  font-size: 3rem;
  transition: all 0.3s ease-in-out;
}
.navegacion--menu--lista {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion--menu--lista {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1.6rem;
  }
}
.navegacion--menu--lista > li {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .navegacion--menu--lista > li {
    margin-bottom: 0;
  }
}
.navegacion--menu--enlace {
  color: #ffffff;
}
@media (min-width: 768px) {
  .navegacion--menu--enlace {
    margin-left: 1.5rem;
  }
}
.navegacion--menu--enlace:hover {
  color: #be6bff;
  font-size: 2rem;
  transition: all 0.3s ease-in-out;
}
.navegacion--menu--enlace:not(:hover) {
  color: #ffffff;
  font-size: 1.8rem;
  transition: all 0.3s ease-in-out;
}

.navegacion--menu--enlace.active {
  color: #be6bff;
  transition: all 0.3s ease-in-out;
}

.banner {
  flex-direction: column;
  padding: 1rem 0;
  gap: 3rem;
}
@media (min-width: 768px) {
  .banner {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    padding: 2rem 0;
  }
}
.banner--texto-bienvenida {
  font-family: "Lekton", sans-serif;
  margin-bottom: 0.7rem;
  font-weight: 400;
  display: inline-block;
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid #be6bff;
  width: 0;
  animation: typing 2.5s steps(16, end) forwards, cursor-blink 0.7s step-end infinite, pause 1s 2.5s forwards, erasing 2s steps(16, end) 3.5s forwards, loop 5.5s infinite 3.5s;
}
.banner--texto-nombre {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1.2rem;
}
.banner--texto-carrera {
  color: #6c63ff;
  font-weight: 900;
  font-size: 2.5rem;
  margin-bottom: 2rem;
}
.banner--iconos {
  justify-content: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .banner--iconos {
    justify-content: start;
  }
}
.banner--icono {
  font-size: 2rem;
  color: #ffffff;
  transition: all 0.3s ease-in-out;
}
.banner--icono:hover {
  color: #be6bff;
  transform: scale(1.1);
  cursor: pointer;
}
.banner--img {
  max-width: 60rem;
}
.banner--texto-email {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
.banner--texto-email--boton {
  border-radius: 1.1rem 0 0 1.1rem;
  padding: 0.7rem;
}
.banner--texto-email--boton:hover {
  background-color: #ffffff;
  color: #1e1e1e;
  cursor: pointer;
  transition: background-color 0.5s ease-in;
  transition: color 0.5s ease-in-out;
}
.banner--texto-email--boton:not(:hover) {
  transition: background-color 0.5s ease-in;
  transition: color 0.5s ease-in-out;
}
.banner--texto-email--boton, .banner--texto-email--input {
  background-color: #1e1e1e;
  border: 1px solid #ffffff;
  margin-bottom: 1rem;
  color: #ffffff;
}

.banner--texto-email--input {
  transition: opacity 0.3s ease, transform 0.3s ease, transform 0.3s ease, background-color 0.3s ease;
  border-radius: 0 1.1rem 1.1rem 0;
  width: 100%;
  font-weight: 400;
  background-color: #1e1e1e;
  border: 1px solid #ffffff;
  margin-bottom: 1rem;
  color: #ffffff;
  text-align: center;
}
@media (min-width: 330px) {
  .banner--texto-email--input {
    font-size: 1.3rem;
    padding: 1.2rem;
  }
}
@media (min-width: 768px) {
  .banner--texto-email--input {
    padding: 1.15rem;
  }
}
@media (min-width: 1024px) {
  .banner--texto-email--input {
    font-size: 2rem;
    padding: 0.75rem;
  }
}
.banner--texto-email--input:focus {
  outline: none;
  border-color: #be6bff;
  box-shadow: 0 0 5px #be6bff;
  transition: all 0.3s ease-in-out;
}
.banner--texto-email--input.email--fade-out {
  opacity: 0;
  transform: translateY(-1rem);
}
.banner--texto-email--input.email--fade-in {
  opacity: 1;
  transform: translateY(0);
}
.banner--texto-email--input.email--copiado {
  background-color: #6c63ff;
  color: #ffffff;
  transition: all 0.3s ease-in-out;
}

.btn-copiado {
  animation: pulse 0.5s ease-in-out;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes erasing {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}
@keyframes cursor-blink {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: #6c63ff;
  }
}
@keyframes pause {
  from, to {
    width: 100%;
  }
}
@keyframes loop {
  0% {
    width: 0;
  }
  45% {
    width: 100%;
  }
  55% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}
.sobre-mi {
  display: flex;
  flex-direction: column;
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .sobre-mi {
    margin-top: 15rem;
    flex-direction: row;
  }
}
.sobre-mi--img {
  margin-bottom: 4rem;
  width: 100%;
  height: 100%;
  max-width: 50rem;
  max-height: 50rem;
}
@media (min-width: 1024px) {
  .sobre-mi--img {
    margin-bottom: 0;
  }
}
.sobre-mi--img:hover {
  transform: translateX(1.5rem) translateY(1.5rem);
  outline: none;
  box-shadow: -11px -10px 9px 5px rgba(108, 99, 255, 0.75);
  -webkit-box-shadow: -11px -10px 9px 5px rgba(108, 99, 255, 0.75);
  -moz-box-shadow: -11px -10px 9px 5px rgba(108, 99, 255, 0.75);
  transition: all 0.3s ease-in-out;
}
.sobre-mi--img:not(:hover) {
  transition: transform 0.3s ease-in-out;
}
.sobre-mi--texto {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 768px) {
  .sobre-mi--texto {
    margin-left: 10rem;
  }
}
.sobre-mi--texto--subtitulo {
  font-family: "Lekton", sans-serif;
  font-size: 2.4rem;
  font-weight: 400;
}
.sobre-mi--texto--titulo {
  font-family: "Lato", sans-serif;
  font-size: 4rem;
  font-weight: 700;
}
.sobre-mi--texto--parrafo {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  margin-top: 2rem;
  line-height: 2.5rem;
}

.header-proyectos {
  font-family: "Lato", sans-serif;
  font-size: 3.5rem;
  font-weight: 900;
  text-align: center;
  margin-top: 15rem;
}

.proyectos {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .proyectos {
    display: grid;
    grid-template-columns: 1fr 6fr;
    gap: 5rem;
    width: 95%;
    max-width: 100rem;
    margin: 0 auto;
  }
}

.proyectos--menu {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 1024px) {
  .proyectos--menu {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem;
    margin-bottom: 0;
  }
}

.swiper {
  width: 100%;
}

.swiper-slide {
  padding: 2rem 0;
}

.proyectos--contenedor {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}
@media (min-width: 768px) {
  .proyectos--contenedor {
    grid-template-columns: 1.5fr 1fr;
    align-items: center;
  }
}

.swiper-button-prev::after,
.swiper-button-next::after {
  color: #ffffff;
  font-weight: 400;
}

.habilidades {
  gap: 1.5rem;
  display: grid;
}
.habilidades--titulo {
  font-size: 4rem;
  font-weight: 900;
  text-align: center;
  margin-top: 15rem;
}
@media (min-width: 768px) {
  .habilidades {
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
.habilidades > div {
  width: 100%;
}
.habilidades > div:hover {
  transform: scale(1.05);
  box-shadow: 0px 21px 14px -15px rgba(190, 107, 255, 0.71);
  -webkit-box-shadow: 0px 21px 14px -15px rgba(190, 107, 255, 0.71);
  -moz-box-shadow: 0px 21px 14px -15px rgba(190, 107, 255, 0.71);
  transition: all 0.3s ease-in-out;
}
.habilidades > div:not(:hover) {
  transition: all 0.3s ease-in-out;
}
.habilidades > div > h2 {
  font-size: 2.5rem;
  font-weight: 900;
  text-align: center;
}

.habilidades > div > div {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.habilidades--herramientas-iconos {
  flex-wrap: wrap;
}

.formacion {
  margin: 10rem 0;
}
.formacion--titulo {
  margin-bottom: 2rem;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  font-size: 3.2rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
.formacion--lista {
  border-inline-start: 1px solid #ffffff;
  position: relative;
}
.formacion--lista--item {
  padding-left: 2rem;
  padding-bottom: 4rem;
}
.formacion--lista--item > .circulo {
  height: 1rem;
  width: 1rem;
  background-color: #ffffff;
  border-radius: 50%;
  position: absolute;
  left: -0.5rem;
  margin-top: 0.7rem;
}
.formacion--lista-titulo {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-size: 2.2rem;
}
.formacion--lista-texto {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  letter-spacing: 0.2rem;
  margin-bottom: 3rem;
}
.formacion--lista-enlace {
  width: max-content;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
  padding: 1rem 1rem;
  border-radius: 1rem;
  color: rgb(0, 91.9101123596, 204.5);
  transition: all 0.3s ease-in-out;
}
.formacion--lista-enlace:hover {
  color: #6c63ff;
  text-decoration: underline;
}

.footer {
  margin: 0;
  padding: 3rem;
  width: 100%;
}
@media (min-width: 768px) {
  .footer {
    display: flex;
    gap: 2rem;
    justify-content: center;
    margin-bottom: 2rem;
  }
}

.proyectos--mockup {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: auto;
}
.proyectos--mockup--img {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .proyectos--mockup--img {
    max-width: 45rem;
    max-height: 45rem;
  }
}
@media (min-width: 768px) {
  .proyectos--mockup--img-tablet, .proyectos--mockup--img-phone {
    max-width: 30rem;
    max-height: 35rem;
    margin: 0 0 6rem 12rem;
  }
}

.mockup--webp,
.mockup--png {
  margin-bottom: 5rem;
}

.proyectos--menu--icono {
  display: flex;
  justify-content: center;
  width: 5rem;
  height: 5rem;
  margin-bottom: 2rem;
  padding-bottom: 0.8rem;
}
.proyectos--menu--icono:hover {
  cursor: pointer;
  color: #be6bff;
  transform: scale(1.1);
  transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.proyectos--menu--icono:not(:hover) {
  transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.active {
  color: #be6bff;
  transform: scale(1.1);
  transition: color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.proyecto--resumen-titulo {
  font-family: "Lekton", sans-serif;
  font-size: 4rem;
